@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@700&display=swap);
.facebook-login-button-component{background-color:var(--facebook-blue) !important}

.header-component .navbar{padding:0 !important;height:0;z-index:1}.header-component .navbar .logo{position:absolute;width:100px;height:100px;top:0em;left:0em}.header-component .navbar button{margin-top:1em}.header-component .navbar .nav-item{display:flex}.header-component .navbar .nav-item .button-component{background-color:var(--green)}

.leaderboard-modal-component .modal-content{min-height:90vh}

.link-component,.link-component:hover{color:white}

.loading-component{display:flex;align-items:center;flex-direction:column;width:100%;-webkit-animation:fadein 2s;animation:fadein 2s}@keyframes fadein{from{opacity:0}to{opacity:1}}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}

.spinner-component{display:flex;align-items:center;flex-direction:column}.spinner-component .icon-spin{-webkit-animation:icon-spin 2s infinite linear;animation:icon-spin 2s infinite linear}@-webkit-keyframes icon-spin{0%{transform:rotate(0deg)}100%{transform:rotate(359deg)}}@keyframes icon-spin{0%{transform:rotate(0deg)}100%{transform:rotate(359deg)}}

.municipality-progress-component{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-top:0.3em}.municipality-progress-component .name{color:white;text-shadow:0 0 0.2em black;margin-bottom:-0.8em;font-weight:500}.municipality-progress-component .progress{margin-top:1em;width:20em}.municipality-progress-component .progress .offset{background-color:var(--progress-bar-fill)}.municipality-progress-component .progress .remaining{background-color:var(--progress-bar-trail)}

.home-page .intro_section{background-image:url("/assets/jon-flobrant-oRUQsTX9Zv0-unsplash.jpg");width:100vw;height:100vh;margin:0;background-size:cover;background-attachment:fixed;display:flex;justify-content:center;flex-flow:column nowrap}.home-page .intro_section .counter_and_logo_outer_container{width:100%;padding-top:2em;position:relative;justify-content:center;display:flex;flex-flow:column-nowrap}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container{display:flex;flex-flow:column nowrap;margin:auto;margin-top:18vh;position:absolute;width:20%}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .logo{-webkit-filter:drop-shadow(2px 4px 10px white);filter:drop-shadow(2px 4px 10px white);width:100%;max-width:200px;margin:auto;-webkit-animation-name:fadeIn;animation-name:fadeIn;opacity:0;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-delay:3s;animation-delay:3s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .co2counter{margin:0;text-align:center;width:100%;font-family:'Nunito', sans-serif;text-shadow:0 0 0.2em #000;color:white;-webkit-animation-name:fadeIn;animation-name:fadeIn;font-weight:bold;opacity:0;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-delay:1s;animation-delay:1s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .co2counter p:first-child{font-size:1.5em}.home-page .intro_section .counter_and_logo_outer_container .counter_and_logo_container .co2counter p{margin:0;font-size:1.25em}.home-page .intro_section .counter_and_logo_outer_container .progressbar-container{width:70%;height:35%;max-width:600px}.home-page .intro_section .municipalities-progress{display:flex;justify-content:center;align-items:center;flex-direction:column}.home-page .intro_section .municipalities-progress button{margin-top:0.5em;background-color:var(--green)}.home-page .chart_section{margin:auto;width:70vw;height:100vw;padding:5vw}.home-page .chart_section .chart_container .header_style{text-align:center}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}

:root{--purple: #6F00FF;--purple-light: #a966ff;--purple-soft: #e2ccff;--progress-bar-trail: #a6a8b6;--progress-bar-fill: #fff234;--chart-tooltip-color: #000;--chart-tooltip-background: #eee;--white: #fff;--green: #3A8617;--facebook-blue: #4267B2;--blue: #1493FF;--pink: #ff1493}html{overflow-x:hidden}.route-container{margin-top:2em}

